/* Game.css */

/* Container del Marketplace */
.marketplace {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* Titoli */
  .marketplace h2 {
    text-align: center;
  }
  
  .marketplace h3 {
    margin: 10px 0;
  }
  
  /* Mystery Box */
  .mystery-box {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .mystery-box img {
    width: 200px;
    height: 200px;
  }
  
  .sell-eggs, .eggs-for-sale, .my-eggs-for-sale {
    margin-top: 20px;
  }
  
  .egg-item {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 5px;
  }
  
  /* Input fields */
  .egg-item input {
    margin: 0 5px;
    padding: 3px;
  }
  
  /* Modal */
  .modal {
    display: none;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .modal.open {
    display: block;
  }

  .modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    width: 60%;
  }
  
  .modal-content .close {
    float: right;
    cursor: pointer;
  }
  
  .modal-content table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .modal-content th, .modal-content td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .modal-content th {
    background-color: #f2f2f2;
    text-align: left;
  }
  
  .modal-content img {
    width: 50px;
    height: 50px;
  }
  
  .selected-egg-details {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  button {
    padding: 5px 10px;
    margin: 5px 0;
    cursor: pointer;
  }
  
  
.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }